import React, { Component } from 'react';
import style from './style.scss';

import photoImg from './photo.jpg';

class AboutSection extends Component {
  render() {

    return (
      <div className={style.root} id="About">
        <div className={style.content}>
          <div className={style.photo}>
            <img src={photoImg} alt="About Olga"/>
          </div>
          <div className={style.text}>
            <p>
              I was born in Kiev, Ukraine and I was very active child  I love sports. 
            </p>
            <p>
              At the age of 13 I joined fitness class.I quickly became interested learning everything I could about nutrition.  I tested many diets, especially those that were scientifically proven. I should say it was not so easy for me to change my food habits completely.  I was practicing yoga for several years. I even tried vegetarian diet.  I learned to stay disciplined, determined and goal minded. As a result of my determination, felt healthier, energized, happy and inspired to reach higher goals and standards for myself. 
            </p>
            <p>
              In 2006 I began lifting serious weights and with assistance of yoga, and nutritionally rich meals, I noticed how my body began to change fast. I concentrated on better understanding how protein functions in the body and the effects of proper diet application. I became an expert.
            </p>
            <p>
              Despite challenges in live, such us destroyed marriage followed by depression and other general complications associated with living in poor country, I remained motivated and goal oriented. Workouts at the gym kept me focused on my goals.
            </p>
            <p>
              My first real SHINNING STAR moment was when I met President of Ukrainian Federation of Bodybuilding and asked him to give me a trainer to get ready for the contest. I worked really hard to achieve my goal and compete professionally on stage. In the year 2008, I won Ukrainian Championships in a Figure division. I was very happy and that victory strengthened my desire to improve and go further to win. But more than that, I knew I had found my passion.
            </p>
            <p>
              Since then, my life became even more scheduled and planned than ever. Now I knew, I can definitely reach my goals - about family, about sport and career. I wanted to achieve them whatever it takes.
            </p>
            <p>
              In 2012 - I won all the contests in Ukraine, I won bronze medal of European Championships and then bronze medal of World Championships. In 2013 I became a winner of European Championships and overall World Champion - the highest title in amateur bodybuilding.  Later, in 2014 I became overall winner of World Ladies Cup and my IFBB Pro card. My Pro debut was on Mozolani Classic in Zilina, Slovakia in May 2014. 
            </p>
            <p>
              In October-November 2014 I began competing at the professional shows in USA. It was my first time when I came to the Mecca of Bodybuilding - Golds Gym Venice
            </p>
            <p>
              Now, that I am here, I am excited to share a wealth of knowledge and experience among my current and prospect clients.
            </p>
            <p>
              I believe that my mission is to give people that direction in life to be happy and motivate them to reach for even better brighter future.
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default AboutSection;



