import React, { Component } from 'react';

import Modal from 'react-modal';

import style from './style.scss';

class TwelveWeeks extends Component {
  constructor(props){
    super(props)

    this.state = {
      isOpen: props.isOpen || false,
    }
  }

  componentWillReceiveProps( nextProps) {
    const { isOpen } = this.props;

    if (isOpen !== nextProps.isOpen) {
      this.setState({isOpen: nextProps.isOpen});
    }
  }

  render() {

    const { isOpen } = this.state;
    const { close } = this.props;

    if (!isOpen) {
      return false;
    }

    return (
      <Modal
        isOpen={isOpen}
        //onAfterOpen={afterOpenFn}
        onRequestClose={close}
        //closeTimeoutMS={n}
        style={{
          overlay: {zIndex: 1000},
          content: {top: '6.2vw', left: '8.2vw', right: '8.2vw'}
        }}
        //contentLabel="Modal"
      >
        <div className={style.root}>
          <header>
            <h2>12 Weeks online PROGRAM</h2>
          </header>
          <div className={style['form-wrapper']}>
            <p>
              Olga Karavayeva - professional IFBB Figure athlete, World and European champion, personal trainer, nutritionist and a fitness model. Olga is committed to share acquired knowledge and experience with people who are ready to take charge of their lives and will benefit from personalized custom-made program that will help people achieve their goals. Personal trainer is there for you 24/7.
            </p>
            <p>
              All changes in your life start from you - from changing your mindset, changing your body and food habits. Nowadays we really have a high level of stress in everyday life - either it is connected with money, carrier or with relationship or other factors.
              <br/>If you made a decision to change your life for better, you should start from yourself. You cant expect for life to change until you are committed to change yourself first.
            </p>
            <p>
              No more guess-work when it comes to fitness and nutritional diet. Each workout and meal are planned based on your current fitness level and goal. All you have to do is login to the mobile app, tablet or computer and follow your custom plan.Its that easy!
              Each step of the way is closely monitored by the coach and an immediate feedback is produced based on clients performance and needs.
            </p>
            
            <p className={style['are-you']}>
              <br/>Are you ready to change your for better? 
              <br/>Are you ready to enjoy every day of your life? 
              <br/>Are you ready to become happier? 
              <br/>Are you ready to get in shape?
            </p>

            <div className={style['program-for']}>
              <div className={style['column']}>
                <h3>This program is for people who:</h3>
                <br/>Feel unsatisfied with their present life 
                <br/>Want to improve the quality of their life
                <br/>Want to get healthy and get fit
                <br/>Want to lose weight or gain muscles
                <br/>Want to be more attractive and self-confident 
                <br/>Want to know how to eat healthy
                <br/>
                <br/>...or you just want to make a sexy butt
              </div>
              <div className={style['column']}>
                <h3>What you get during these 12 weeks</h3>
                <br/>You learn how to organize your life
                <br/>You learn how to eat healthy
                <br/>You learn make healthy choices
                <br/>You learn how to make good habits 
                <br/>You learn how to change your body
                <br/>You learn how to lose weight and/or build muscles
                <br/>
                <br/>... and gain confidence to set new life goals  
              </div>
            </div>
            
            <h3>12 week custom workout program includes:</h3>            
            <ul>
              <li>Personalized nutrition plan</li>
              <li>12, weekly, 1 on 1 accountability calls</li>
              <li>24/7 messaging access</li>
              <li>12 weekly lessons (healthy habits, morning routine, mindset, community etc)</li>
            </ul>

            <h3>Who should apply for this program:</h3>
             
            <h4>Get the body youve always wanted.</h4>
            Lose weight, build strength, have more energy. Whatever your goals, I will help you get there.
            
            <h4>Tailored just for you, diet</h4>
            Instead of another complex diet program to follow, get ready to have one less thing to worry about. Ill sweat the details so you can breathe easily and focus only on the next step in front of you.

            <h4>Nutrition and fitness that fit into your life.</h4>
            Ill start with you  your life, needs, and goals. Then well develop a personalized plan to help you eat, move, and live better  in a way that fits into your real, everyday life.

            <h4>Small practices. Massive results.</h4>
            Ill break your complex nutritional goals into small, strategic daily practices that add up to massive body transformation. Youll develop healthy habits that become second nature and last a lifetime.

            <h4>Guidance and support.</h4>
            I will give you accountability, direction, and support every step of the way. Ill help you stay consistent, no matter what life throws at you.
            
            <h3>Proven strategies:</h3>
            Youve probably tried multiple diet and workout programs in the past. And youve concluded that they simply dont work in the long run.
            <br/>
            <br/>Instead of the all-or-nothing dieting approach, I use a sustainable, practice-based approach to build healthy habits into your life, one day at a time.

            <h3>The result: </h3>
            Youll lose the weight (and inches) you havent been able to shed for years. Youll develop a better relationship with food. And youll say goodbye to dieting, food obsessions, and body anxieties for good.
            <br/>
            <br/>Take one small step today toward getting into the best shape of your life.
            <br/>
            <br/>
            <button className={style['close']} type="button" onClick={close}>
              APPLY FOR THE PROGRAM 
              <br/> TODAY
            </button>
          </div>
        </div>
      </Modal>
    );
  }
}

export default TwelveWeeks;




