import React, { Component } from 'react';

import Header from 'components/Header';
import Footer from 'components/Footer';
import CarouselSection from './CarouselSection';
import ProductsSection from './ProductsSection';
import EveryDaySection from './EveryDaySection';
import AboutSection from './AboutSection';
import PresentSituationSection from './PresentSituationSection';
import TestimonialsSection from './TestimonialsSection';
import TheOnlySection from './TheOnlySection';
import FuturedVideoSection from './FuturedVideoSection';
import ReleasePotentialSection from './ReleasePotentialSection';

import ConfirmFormDialog from 'components/dialogs/ConfirmForm';
import TwelveWeeksDialog from 'components/dialogs/TwelveWeeks';


class MainPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      isConfirmOpen: false,
      isTwelveWeeksOpen: false,
      params: false
    }
  }

  openConfirm = (params = false) => {
    this.setState({isConfirmOpen: true, params})
  }

  openTwelveWeeks = () => {
    this.setState({isTwelveWeeksOpen: true})
  }

  closeConfirm = () => {
    this.setState({isConfirmOpen: false})
  }

  closeTwelveWeeks = () => {
    this.setState({isTwelveWeeksOpen: false})
  }

  render() {
    const { isConfirmOpen, isTwelveWeeksOpen } = this.state;
    return (
      <div>
        <ConfirmFormDialog isOpen={isConfirmOpen} params={this.state.params} close={this.closeConfirm} />
        <TwelveWeeksDialog isOpen={isTwelveWeeksOpen} close={this.closeTwelveWeeks} />
        <Header />
        <CarouselSection scrollToBottom={this.props.scrollComponent.scrollToBottom}/>
        <ProductsSection opens={{confirm: this.openConfirm, twelveWeeks: this.openTwelveWeeks }} />
        <EveryDaySection />
        <AboutSection />
        <PresentSituationSection />
        <TestimonialsSection />
        <TheOnlySection />
        <FuturedVideoSection />
        <ReleasePotentialSection open={this.openConfirm} />
        <Footer />
      </div>
    );
  }
}

export default MainPage;



